import s from './explorer-block-page.module.scss';
import BackButton from "../../shared/buttons/back-button/BackButton"
import { Tabs } from 'components/shared/tabs/tabs';
import { BlocksTable } from './block-table/block-table';
import { DataTable} from './data-table/data-table'
import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { RawContent } from './raw-content/raw-content';
import { candyStore } from 'store/candy/core/candy.store';

enum EManageTabId {
  summary = "summary",
  data = "data",
  raw = "raw",
}

interface IManageTab {
  id: EManageTabId;
  text: string;
}

export const ExplorerBlockPage = () => {

  const [tab, setTab] = useState(EManageTabId.summary)

  const location = useLocation()
  const [idBlock, setIdBlock] = useState("");

  const manageTabs: IManageTab[] = [
    { id: EManageTabId.summary, text: "Summary" },
    { id: EManageTabId.data, text: "Data" },
    { id: EManageTabId.raw, text: "Raw" },
  ];

  const changeTab = (tab:any) =>{
    setTab(tab.id);
  }

  useEffect(()=>{
    
    const splitUrl = location.pathname.split('/');
    setIdBlock(splitUrl[splitUrl.length - 1]);
  },[])

  return (
    <article className={`${s.blockPage} whiteCard`}>
      <BackButton/>
        <div className={s.title}>
          Block #{idBlock}
        </div>
        <Tabs
          className={s.tabs}
          onSelect={changeTab} 
          tabs={manageTabs}
          type="button"
        />
        {tab === EManageTabId.summary ?
          <BlocksTable/>:tab === EManageTabId.data ? <DataTable/> : <RawContent/>
        }
        
        
    </article>
  );
};
