import { range } from '../../../../functions/range';


export const getPages = (totalPages: number, showPagesCount: number, selectedPage: number) => {
  const pages: number[] = [];
  const offset = Math.floor(showPagesCount / 2);
  const startPage = selectedPage - offset;
  const endPage = Math.min(selectedPage + offset, totalPages);

  for (let page = 1; page < totalPages + 1; page++) {
    if (startPage > page) {
      continue;
    }
    pages.push(page);
    if (pages.length === showPagesCount) {
      return pages;
    }
  }

  if (pages.length < showPagesCount) {
    return range(Math.max(endPage - (showPagesCount - 1), 1), endPage);
  }

  return pages;
};

