import { SetState } from '../../../../types/utils/set-state';

type Props = {
  selectedPage: number
  setSelectedPage: SetState<number>
  totalPagesCount: number
}

export const useGoToPage = ({ selectedPage, setSelectedPage, totalPagesCount }: Props) => {
  const isFirst = selectedPage === 1;
  const goToFirst = isFirst
    ? null
    : () => setSelectedPage(1);

  const goBack = isFirst
    ? null
    : () => setSelectedPage(page => page - 1);

  const isLast = selectedPage === totalPagesCount;
  const goToLast = isLast
    ? null
    : () => setSelectedPage(totalPagesCount);

  const goForward = isLast
    ? null
    : () => setSelectedPage(page => page + 1);

  return {
    goToFirst,
    goBack,
    goToLast,
    goForward
  };
};
