import React, { FC, MouseEvent, useCallback } from "react";
import { ReactComponent as LeftArrowIcon } from "../../../../assets/images/left-arrow.svg";
import { Button, ButtonProps } from "../button/button";
import useMedia from "../../../../hooks/useMedia";
import clsx from "clsx";
import { useHistory } from "react-router-dom";

import styles from "./BackButton.module.scss";

export interface IBackButton extends Omit<ButtonProps, "type" | "size"> {
  withComeBack?: boolean;
}

const BackButton: FC<IBackButton> = ({
  children,
  withComeBack = true,
  buttonProps,
  ...otherProps
}) => {
  const history = useHistory();
  const { isMobile } = useMedia();

  const { className, onClick } = buttonProps || {};

  const onComeBackHandler = useCallback(() => history.goBack(), [history]);

  const onClickHandler = useCallback(
    (e: MouseEvent<HTMLButtonElement>) => {
      withComeBack && onComeBackHandler();

      onClick?.(e);
    },
    [withComeBack, onComeBackHandler, onClick]
  );

  return (
    <Button
      buttonProps={{
        ...buttonProps,
        onClick: onClickHandler,
        className: clsx(styles.back, className),
      }}
      leftIcon={<LeftArrowIcon />}
      {...otherProps}
      type="tertiary"
      size="medium"
    >
      {!isMobile && (children || "Return")}
    </Button>
  );
};

export default BackButton;
