import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { blocksStore } from 'store/candy/explorer/blocks.store';
import { BlockData } from '../block-table/table';
import s from './raw-content.module.scss'

export const RawContent = () => {
    const location = useLocation();
    const [data, setData] = useState<BlockData>()

  const splitUrl = location.pathname.split('/');
  const index = splitUrl[splitUrl.length - 1];

  const getData = async () =>{
    const data = await blocksStore.loadBlockPreview(index).then((res:any) => res)  
    setData(data);
  }
  
  useEffect(()=>{
    getData();
  },[])

  return (
    <div className={s.content}>
        {data?.rawData}
    </div>
  )
}
