import { PaginatorProps } from '../paginator';
import { EXPLORER_PAGE_SIZE } from '../../../../api/consts';
import { useGoToPage } from './useGoToPage';
import { getPages } from './getPages';
import { RefObject, useMemo } from 'react';
import { useShowPageCount } from './use-show-page-count';


export const usePaginator = (
  {
    selectedPage,
    setSelectedPage,
    totalCount,
    pageSize = EXPLORER_PAGE_SIZE
  }: PaginatorProps,
  paginatorRef: RefObject<HTMLDivElement>
) => {
  const totalPagesCount = Math.ceil(totalCount / pageSize);

  const showPagesCount = useShowPageCount(totalPagesCount, paginatorRef);
  const pages = useMemo(
    () => getPages(totalPagesCount, showPagesCount, selectedPage),
    [totalPagesCount, showPagesCount, selectedPage]
  );

  const goToPageActions = useGoToPage({
    selectedPage,
    setSelectedPage,
    totalPagesCount
  });


  return {
    pages,
    setSelectedPage,
    selectedPage,
    ...goToPageActions
  };
};
