import s from './blocks-table.module.scss';
import { observer } from 'mobx-react-lite';
import { BlockData } from '../block-table/table'
import { useEffect, useState } from 'react';
import { blocksStore } from 'store/candy/explorer/blocks.store';
import { useLocation } from 'react-router-dom';
import { BlockType } from 'store/candy/explorer/types/BlockType';
//@ts-ignore
import SyntaxHighlighter from 'react-syntax-highlighter';
//@ts-ignore
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';
//@ts-ignore
import beautify from "js-beautify";

type Props = {
  className?: string
}


export const Table = observer(({ className }: Props) => {
  const location = useLocation();

  const [data, setData] = useState<BlockData>()

  const splitUrl = location.pathname.split('/');
  const index = splitUrl[splitUrl.length - 1];
  
  const getData = async () =>{
    const data = await blocksStore.loadBlockPreview(index).then((res:any) => res)  
    setData(data);
  }
  useEffect(()=>{
    getData();
  },[]);

  console.log('table data', data);
  if(data?.type === BlockType.ContractDeploy){
    return (
      <div className={`${s.table} ${className}`}>
      <div className={`${s.header} ${s.row}`}>
        <div className={`${s.cell} + ${s.head}`}>Attribute</div>
        <div className={`${s.cell} + ${s.head}`}>Value</div>
      </div>
        <>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Sender</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.sender}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Code hash</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.codeHash}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Contract code</div>
            <div className={`${s.cell} + ${s.value} + ${s.code}`}>
            <SyntaxHighlighter
                language="jsx"
                wrapLongLines
                style={docco}
                className={s.code}
              >
                {data?.contractCode ? beautify(data?.contractCode) : "[]"}
              </SyntaxHighlighter>
            </div>
          </div>
        </>
    </div>
    )
  }
  else if(data?.type === BlockType.ContractCall){
    return (
      <div className={`${s.table} ${className}`}>
      <div className={`${s.header} ${s.row}`}>
        <div className={`${s.cell} + ${s.head}`}>Attribute</div>
        <div className={`${s.cell} + ${s.head}`}>Value</div>
      </div>
        <>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Sender</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.sender}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Address</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.address}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Method</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.method}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Arguments</div>
            <div className={`${s.cell} + ${s.value}`} style={{color:"#FF3030"}}>
                {data?.arguments ? JSON.stringify(data?.arguments, null, 2) : '[]' }
            </div>
          </div>
        </>
    </div>
    )
  }
  else{
    return <div style={{marginTop:40, marginLeft:40}}>Data for this block not found</div>
  }
});
