import { observer } from 'mobx-react-lite';
import { blocksStore } from '../../../../store/candy/explorer/blocks.store';
import Spinner from '../../../shared/spinner/Spinner';
import { Table } from './table';
import { Cards } from './cards';
import { useEffect } from 'react';

type Props = {
  className?: string
}

export const EventsTable = observer(({ className }: Props) => {
  if (blocksStore.isEventsLoading) {
    return <Spinner isLoading={blocksStore.isEventsLoading}/>;
  }
  return (
    <div className={className}>
      <Table/>
      <Cards/>
    </div>
  );
});
