import { request } from '../blockchain/plugins/request';

export const NODES = 'NODES';

type GetNodesResponse = {
  result: string
}

export const getCountNodes = () =>
  request<GetNodesResponse>(`contracts/ecma/callMethod/1/getNodesCount`, {
    data: {},
    method: 'post',
    contractAddress: ''
  })
    .then(res => {     
        return res.result
    });
