import { candyStore } from '../core/candy.store';
import { makeAutoObservable } from 'mobx';
import { EXPLORER_PAGE_SIZE } from '../../../api/consts';
import { ChainBlock } from '../../../types/history-item';
import { whenCandyIsReady } from '../../reactions/when-candy-is-ready';
import { whenBlockHeightIsFetched } from '../../reactions/when-block-height-is-fetched';
import { Block } from '../../../components/pages/explorer/data';
import { performBlock } from '../../../functions/chain/perform-block/perform-block';
import { PerformedEvent } from '../../../functions/chain/perform-event';
import { searchEvents } from '../../../api/search-events';
import { toast } from 'react-toastify';
import { candyEventsStore } from '../core/candy-events.store';
import { BlockType } from './types/BlockType';


class BlocksStore {
  selectedPage = 1;

  events: PerformedEvent[] = [];
  eventsCount = 0;
  isEventsLoading = false;
  search = '';

  blocks: Block[] = [];
  isBlocksLoading = false;
  hideEmptyBlocks = false;

  get totalCount() {
    return this.search
      ? this.eventsCount
      : candyEventsStore.blockHeight;
  }


  constructor() {
    makeAutoObservable(this, {}, { autoBind: true });
  }

  fetchItems(search: string, hideEmptyBlocks: boolean, selectedPage: number) {
    this.search = search;
    this.hideEmptyBlocks = hideEmptyBlocks;
    this.selectedPage = selectedPage;
    this.events = [];
    this.blocks = [];
    if (search) {
      this.fetchEvents(selectedPage).then();
    } else {
      this.fetchBlocks(selectedPage).then();
    }
  }

  async fetchEvents(page: number) {
    await whenBlockHeightIsFetched();
    this.isEventsLoading = true;
    searchEvents(this.search, page)
      .then(({ events, count }:any) => {
        this.events = events;
        this.eventsCount = count;
      })
      .catch(() => toast.error('Failed to search'))
      .finally(() => this.isEventsLoading = false);
  }

  async fetchBlocks(page: number) {
    await whenBlockHeightIsFetched();
    this.isBlocksLoading = true;

    const blockId = candyStore.candy.blockHeight;
    const offset = (page - 1) * EXPLORER_PAGE_SIZE;
    candyStore.candy.loadBlocks(blockId, EXPLORER_PAGE_SIZE, offset, this.hideEmptyBlocks);
  }

  updateExplorerBlocks(fetchedBlocks: ChainBlock[]) {
    const currentPageSkip = (this.selectedPage - 1) * EXPLORER_PAGE_SIZE;
    const maxBlockIndex = candyEventsStore.blockHeight - currentPageSkip;
    const minBlockIndex = maxBlockIndex - EXPLORER_PAGE_SIZE;

    for (const block of fetchedBlocks) {
      const isOnSelectedPage =
        block.index <= maxBlockIndex &&
        block.index >= minBlockIndex;

      if (isOnSelectedPage && !this.blocks.some(b => b.index === block.index)) {
        this.blocks.push(performBlock(block));
      }
    }

    this.blocks = this.blocks.sort((block1, block2) => block2.index - block1.index);
    this.isBlocksLoading = false;
  }

  onBlocksResponse(fetchedBlocks: ChainBlock[]) {
    this.updateExplorerBlocks(fetchedBlocks);
  }

  async loadBlockPreview(index:any){
    return new Promise((resolve)=>{   
      candyStore.candy.loadResource(index, async (err:any, block:any, rawBlock:any) => {
        console.log('rawBlock',rawBlock);
        console.log('block',block);
        let data = {
          blockHash: rawBlock.hash,
          previousHash: rawBlock.previousHash,
          index: rawBlock.index,
          timestamp: rawBlock.timestamp,
          type: block.type,
          fee: rawBlock.fee,
          size: rawBlock.data.length,
          blockSign: block.sign,
          rawData: rawBlock.data,
          sender: block.state ? block.state.sender : "",
          address: block.address,
          method: block.method,
          arguments: block.args,
          codeHash:'',
          contractCode:''
        }
        if(block.type === BlockType.ContractDeploy){
          data.sender = block.state.from;
          data.codeHash = block.state.codeHash;
          data.contractCode = block.ecmaCode;
          resolve(data);
        }
        resolve(data);
      })
    })
  }

  async fetchNewBLocks(height: number, count: number) {
    await whenCandyIsReady();
    candyStore.candy.loadBlocks(height, count, 0, this.hideEmptyBlocks);
  }
}

export const blocksStore = new BlocksStore();
