import s from './paginator.module.scss';
import { SetState } from '../../../types/utils/set-state';
import { usePaginator } from './use-paginator/use-paginator';
import { memo, useRef } from 'react';

export type PaginatorProps = {
  selectedPage: number
  setSelectedPage: SetState<number>
  totalCount: number
  pageSize?: number
}

type Props = {
  className?: string
} & PaginatorProps


export const Paginator = memo(({ className, ...props }: Props) => {
  const ref = useRef<HTMLDivElement>(null);
  const {
    pages, setSelectedPage, selectedPage, goToFirst, goBack, goForward, goToLast
  } = usePaginator(props, ref);

  return (
    <div ref={ref} className={`${s.paginator} ${className}`}>
      {<button disabled={!goToFirst} onClick={() => goToFirst!()} className={s.page}>&laquo;</button>}
      {<button disabled={!goBack} onClick={() => goBack!()} className={s.page}>&lsaquo;</button>}
      {
        pages.map(page =>
          <button
            key={page}
            onClick={() => setSelectedPage(page)}
            className={`${s.page} ${selectedPage === page && s.selected}`}
          >
            {page}
          </button>)
      }
      {<button disabled={!goForward} onClick={() => goForward!()} className={s.page}>&rsaquo;</button>}
      {<button disabled={!goToLast} onClick={() => goToLast!()} className={s.page}>&raquo;</button>}
    </div>
  );
});
