import {
  ChangeEvent,
  FC,
  InputHTMLAttributes,
  ReactElement,
  TextareaHTMLAttributes,
} from "react";
import { useController } from "react-hook-form";
import InputMask from "react-input-mask";

import { ReactComponent as SearchIcon } from "../../../../assets/images/search-icon.svg";
import { normalizeAmount } from "../../../../functions/normalize-amount";
import Typography from "../../typography/typography";

import s from "./input.module.scss";
import clsx from "clsx";

export interface IInput extends InputHTMLAttributes<HTMLInputElement> {
  control:any;
  mask?: string;
  isSearch?: boolean;
  isSolCurrencified?: boolean;
  labelText?: string;
  iconRight?: ReactElement;
  isTextarea?: boolean;
  maxLength?: number;
  withCounter?: boolean;
  textareaProps?: Pick<
    TextareaHTMLAttributes<HTMLTextAreaElement>,
    "cols" | "rows" | "wrap"
  >;
  openCalendar?(): void;
  setValue?(name: string, value: string | number): void;
  maskChar?: string;
  isDate?: boolean;
  handleValueChange?(e: ChangeEvent<HTMLInputElement>): void;
}

export const Input: FC<IInput> = ({
  control,
  name,
  className,
  placeholder,
  isSearch,
  isSolCurrencified,
  mask,
  labelText,
  iconRight,
  children,
  isTextarea,
  maxLength,
  withCounter,
  textareaProps,
  openCalendar,
  maskChar = "_",
  isDate,
  handleValueChange,
  ...otherProps
}) => {
  const {
    field,
    fieldState: { invalid, error },
  } = useController({ control, name: name || "" });

  let inputValue =
    maxLength && field?.value ? field.value.slice(0, maxLength) : field.value;

  isDate && (inputValue = otherProps.value);

  const onFocusHandler = (e: any) => {
    openCalendar?.();
    otherProps.onFocus?.(e);
  };

  const inputProps = {
    ...field,
    ...otherProps,
    value: isSolCurrencified ? normalizeAmount(inputValue || "") : inputValue,
    className: s.input,
    placeholder,
    type: isSolCurrencified ? "number" : "text",
    ...(isDate
      ? {
          value: inputValue,
        }
      : {}),
    onChange: (e: ChangeEvent<HTMLInputElement>) => (
      handleValueChange?.(e), field.onChange?.(e), otherProps.onChange?.(e)
    ),
    onFocus: onFocusHandler,
  };

  if (isSolCurrencified) {
    // @ts-ignore
    inputProps.step = 0.0000_0001;
  }

  return (
    <div className={className}>
      {labelText && (
        <Typography className={s.name} type="open-16">
          {labelText}
        </Typography>
      )}

      <label
        className={clsx(s.wrapper, {
          [s.isSearch]: isSearch,
          [s.isError]: invalid,
        })}
      >
        {mask ? (
          <InputMask mask={mask} {...inputProps} maskChar={maskChar} />
        ) : isTextarea ? (
          /*@ts-ignore TODO resolve ts error*/
          <textarea
            {...inputProps}
            {...textareaProps}
            className={clsx(s.textarea, inputProps.className)}
          />
        ) : (
          <input {...inputProps} min={0} />
        )}

        {iconRight && <span className={s["right-icon"]}>{iconRight}</span>}

        {isSearch && (
          <SearchIcon className={clsx(s.searchIcon, s["right-icon"])} />
        )}

        {isSolCurrencified && <span className={s.currency}>Sol</span>}
      </label>

      {withCounter && (
        <Typography className={s.counter} type="open-14">
          {inputValue?.length || 0} {maxLength && `/${maxLength}`}
        </Typography>
      )}

      {error && (
        <Typography className={s.error} type="open-14">
          {error.message}
        </Typography>
      )}
    </div>
  );
};
