import { request } from '../blockchain/plugins/request';
import { EXPLORER_PAGE_SIZE } from './consts';
import { ChainEvent } from 'types/chain-event';
import { performEvent } from '../functions/chain/perform-event';

type SearchEventsResponse = {
  data: {
    count: number
    list: ChainEvent[]
  }
}

export const searchEvents = (search: string, page: number) => {
  const offset = (page - 1) * EXPLORER_PAGE_SIZE;
  const limit = EXPLORER_PAGE_SIZE;

  return request<SearchEventsResponse>(
    `ertc/search/validation/${encodeURIComponent(search)}`,
    {
      contractAddress: '',
      method: 'post',
      data: { offset, limit }
    }
  )
    .then((res) => {
      console.log(res.data);
      
      return res.data
    })
    .then(({ count, list }) => ({
      events: list.map(performEvent),
      count
    }));

};
