import { getAge } from './get-age';
import { detectBlockType } from './detect-block-type';
import { getSize } from './get-size';
import { ChainBlock } from '../../../types/history-item';
import { Block } from '../../../components/pages/explorer/data';

export const performBlock = (block: ChainBlock): Block => ({
  index: block.index,
  age: getAge(new Date(block.startTimestamp)),
  type: detectBlockType(block),
  size: getSize(block),
  hash: block.hash
});
