import s from './blocks-table.module.scss';
import { observer } from 'mobx-react-lite';
import { useHistory, useLocation } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { blocksStore } from 'store/candy/explorer/blocks.store';
import { candyStore } from 'store/candy/core/candy.store';

type Props = {
  className?: string
}
export type BlockData = {
  blockHash: string,
  previousHash: string,
  index: string,
  timestamp: string,
  type: string,
  fee: string,
  PreviosBlock:string,
  size:string,
  nextBlock:string,
  blockSign: string,
  sender: string,
  address: string,
  method: string,
  arguments: string,
  rawData:string,
  codeHash:string,
  contractCode:string
}

export const Table = observer(({ className }: Props) => {
  const location = useLocation();
  const history = useHistory()

  const splitUrl = location.pathname.split('/');
  const index = splitUrl[splitUrl.length - 1];
  
  const previousBlock = Number(index) - 1;
  const nextBlock = Number(index) + 1;

  const [data, setData] = useState<BlockData>();

  const getData = async () =>{
    if(!candyStore.isReady){
      candyStore.start();
    }
    const data = await blocksStore.loadBlockPreview(index).then((res:any) => res)
    setData(data);
  }
  useEffect(()=>{
    getData();
  },[index])

  const goToBlock = (blockIndex: number) => {
    if(blockIndex == 0){
      return
    }
    history.push(`/explorer/block/${blockIndex}`)
  }

  return (
    <div className={`${s.table} ${className}`}>
      <div className={`${s.header} ${s.row}`}>
        <div className={`${s.cell} + ${s.head}`}>Attribute</div>
        <div className={`${s.cell} + ${s.head}`}>Value</div>
      </div>
        <>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Block Hash</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.blockHash}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Previous block hash</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.previousHash}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Index</div>
            <div className={`${s.cell} + ${s.value}`}><a className={s.linkTable}>{data?.index}</a></div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Timestamp</div>
            <div className={`${s.cell} + ${s.value}`}>{new Date(data?.timestamp!).toUTCString()}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Fee</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.fee}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Previous block</div>
            <div className={`${s.cell} + ${s.value}`}><a onClick={() => goToBlock(previousBlock)} className={s.linkTable}>{previousBlock}</a></div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Size</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.size}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Block type</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.type}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Block sign</div>
            <div className={`${s.cell} + ${s.value}`}>{data?.blockSign}</div>
          </div>
          <div className={s.row}>
            <div className={`${s.cell} + ${s.attribute}`}>Next block</div>
            <div className={`${s.cell} + ${s.value}`}><a onClick={() => goToBlock(nextBlock)} className={s.linkTable}>{nextBlock}</a></div>
          </div>
        </>
    </div>
  );
});
