import { toPlainString } from './to-plain-string'

const limitFractional = (num: string, digitCount = 8) => {
  const [integer, fractional] = num.split(/[.,]/)
  if (fractional) {
    return `${integer}.${fractional.slice(0, digitCount)}`
  }
  return integer
}

export const normalizeAmount = (num: string) => {
  const noFractional = !num.includes(',') && !num.includes('.')

  if (noFractional || num.startsWith(',') || num.startsWith('.')) {
    return num
  }

  return toPlainString(limitFractional(num))
}
