import s from './info-item.module.scss'

type Props = {
  name: string
  value: string | number | undefined
  isHighlighted?: boolean
}


export const InfoItem = ({ name, value, isHighlighted }: Props) => {
  return (
    <div className={s.wrapper}>
      <div className={s.name}>{name}</div>
      <div className={`${s.value} ${isHighlighted && s.highlighted}`}>{value}</div>
    </div>
  )
}
