import s from './blocks-table.module.scss';
import { blocksStore } from '../../../../store/candy/explorer/blocks.store';
import { observer } from 'mobx-react-lite';
import { Block } from '../data';
import { useHistory } from 'react-router-dom';


type Props = {
  className?: string
}


export const Table = observer(({ className }: Props) => {
  const history = useHistory();

  function renderBlock(block: Block) {
    const { index, type, age, size } = block;
    console.log('block', block);

    function redirectTo() {
      history.push(`/explorer/block/${index}`);
    }

    return (
      <div key={index} className={s.row}>
        <div className={s.cell}><a className={s.redirectBtn} onClick={redirectTo}>{index}</a></div>
        <div className={s.cell}>{type}</div>
        <div className={s.cell}>{age}</div>
        <div className={s.cell}>{size}</div>
      </div>
    )
  }

  return (
    <div className={`${s.table} ${className}`}>
      <div className={`${s.header} ${s.row}`}>
        <div className={s.cell}>#</div>
        <div className={s.cell}>Type</div>
        <div className={s.cell}>Age</div>
        <div className={s.cell}>Size in bytes</div>
      </div>
      {
        blocksStore.blocks.map((_) => renderBlock(_))
      }
    </div>
  );
});
