import React, { FC } from "react";

import clsx from "clsx";

import style from "./typography.module.scss";

export type TTypographyType =
  | "open-24"
  | "open-20"
  | "open-14"
  | "open-18"
  | "open-16";

export interface ITypography {
  className?: string;
  type: TTypographyType;
  component?: "button" | "div" | "span" | "h1" | "h2" | "li";
  onClick?: () => void;
  dangerouslySetInnerHTML?: any;
  lineHeight?: string;
  letterSpacing?: string;
  fontWeight?: string;
}

const Typography: FC<ITypography> = ({
  children,
  type,
  component: Component = "div",
  className = "",
  dangerouslySetInnerHTML,
  fontWeight,
  letterSpacing,
  lineHeight,
  ...otherProps
}) => {
  return (
    <Component
      {...otherProps}
      className={clsx(style.typography, className, {
        [style[type || ""]]: type,
      })}
      {...((fontWeight || letterSpacing || lineHeight) && {
        style: {
          ...(fontWeight && { fontWeight }),
          ...(letterSpacing && { letterSpacing }),
          ...(lineHeight && { lineHeight }),
        },
      })}
    >
      {children}
    </Component>
  );
};

export default Typography;
