import React, {
  forwardRef,
  Ref,
  useEffect,
  useImperativeHandle,
  useState,
} from "react";
import clsx from "clsx";
import { AnimateSharedLayout, motion } from "framer-motion";

import { SetState } from "../../../types/utils/set-state";

import s from "./tabs.module.scss";

export type ITab = {
  id?: string;
  text: string;
  element?: JSX.Element;
  disabled?: boolean;
};

type TabProps = {
  tab: ITab;
  selectedTab: ITab;
  setSelectedTab: SetState<ITab>;
  isSmallTabs: boolean;
  isAllWidthTabs: boolean;
  type: "button" | "fullButton" | "line";
};

const Tab = ({
  type,
  tab,
  selectedTab,
  setSelectedTab,
  isSmallTabs,
  isAllWidthTabs,
}: TabProps) => {
  const isActive = tab.text === selectedTab.text;

  const isLineType = type === "line";
  const isButtonType = type === "button";
  const isFullButtonType = type === "fullButton";

  return (
    <div
      onClick={() => setSelectedTab(tab)}
      className={clsx(s.tab, {
        [s.active]: isActive,
        [s.smallTab]: isSmallTabs,
        [s.isDisabled]: tab.disabled,
        [s.isAllWidthTabs]: isAllWidthTabs,
        [s.tab_button]: isButtonType,
        [s.tab_fullButton]: isFullButtonType,
      })}
    >
      {tab.text}
      {isActive && isLineType && (
        <motion.div
          layoutId={"selected-tab-underline"}
          className={s.selectedTabUnderline}
          initial={true}
        />
      )}
    </div>
  );
};

export type TabsHandle = {
  selectTab: (tabText: string) => void;
};

type Props<T> = {
  tabs: ITab[];
  isSmallTabs?: boolean;
  className?: string;
  isHomePage?: boolean;
  isAllWidthTabs?: boolean;
  type?: "button" | "fullButton" | "line";
  onSelect?(tab: ITab | T): void;
  activeTabId?: string;
};

export const Tabs = forwardRef(
  <T,>(
    {
      tabs,
      isSmallTabs,
      className,
      isHomePage,
      isAllWidthTabs,
      type = "line",
      onSelect,
      activeTabId,
    }: Props<T>,
    ref: Ref<TabsHandle>
  ) => {
    const [selectedTab, setSelectedTab] = useState(tabs[0]);

    const isButtonType = type === "button";
    const isFullButtonType = type === "fullButton";

    useImperativeHandle(ref, () => ({
      selectTab(tabText: string) {
        setSelectedTab(tabs.find((tab) => tab.text === tabText)!);
      },
    }));

    useEffect(() => void onSelect?.(selectedTab), [selectedTab]);

    useEffect(() => {
      const activeTab =
        activeTabId && tabs.find(({ id }) => id && activeTabId === id);

      activeTab && setSelectedTab(activeTab);
    }, [activeTabId]);

    return (
      <AnimateSharedLayout>
        <div
          className={clsx(s.tabs, className, {
            [s.isHomePage]: isHomePage,
            [s.tabs_button]: isButtonType,
            [s.tabs_fullButton]: isFullButtonType,
          })}
        >
          {tabs.map((tab) => (
            <Tab
              key={tab.text}
              tab={tab}
              selectedTab={selectedTab}
              setSelectedTab={setSelectedTab}
              isSmallTabs={!!isSmallTabs}
              isAllWidthTabs={!!isAllWidthTabs}
              type={type}
            />
          ))}
        </div>
        {selectedTab?.element}
      </AnimateSharedLayout>
    );
  }
);
