import { useEffect, useState } from "react";


const mobileBreakpoint = 768
const tabletBreakpoint = 1024

const useMedia = (customBreakpoint?: number) => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isTablet, setIsTablet] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [isCustomScreen, setIsCustomScreen] = useState(false)

  useEffect(() => {
    const resizeHandler = () => {
      setIsDesktop(false);
      setIsTablet(false);
      setIsMobile(false);
      setIsCustomScreen(false);

      if(customBreakpoint && window.innerWidth <= customBreakpoint) setIsCustomScreen(true);

      if (window.innerWidth <= mobileBreakpoint) return setIsMobile(true);

      if (window.innerWidth <= tabletBreakpoint) return setIsTablet(true);

      return setIsDesktop(true);
    };

    resizeHandler();

    window.addEventListener("resize", resizeHandler);

    return () => window.removeEventListener("resize", resizeHandler);
  }, []);

  return {
    isDesktop,
    isTablet,
    isMobile,
    isCustomScreen,
  };
};

export default useMedia;
