import { Table } from './table';
import { Cards } from './cards';
import { observer } from 'mobx-react-lite';
import { blocksStore } from '../../../../store/candy/explorer/blocks.store';
import Spinner from '../../../shared/spinner/Spinner';

type Props = {
  className?: string
}


export const BlocksTable = observer(({ className }: Props) => {
  if (blocksStore.isBlocksLoading) {
    return <Spinner/>;
  }

  return <>
    <Table className={className}/>
    <Cards className={className}/>
  </>;
});
