import s from './blocks-table.module.scss';
import timeIcon from '../../../../assets/images/time.svg';
import diamondIcon from '../../../../assets/images/diamond.svg';
import { blocksStore } from '../../../../store/candy/explorer/blocks.store';
import { observer } from 'mobx-react-lite';
import { useHistory } from 'react-router-dom';
import { Block } from '../data';


type Props = {
  className?: string
}


export const Cards = observer(({ className }: Props) => {
  const history = useHistory();

  function renderBlock(block: Block) {
    const { index, type, age, size } = block;

    function redirectTo() {
      history.push(`/explorer/block/${index}`);
    }

    return (
      <div key={index} className={s.card}>
        <div className={s.caption}>
          <div className={s.id}><a className={s.redirectBtn} onClick={redirectTo}>#{index}</a></div>
          <div className={s.type}>{type}</div>
        </div>
        <div className={s.info}>
          <div className={s.age}>
            <img src={timeIcon} alt="" className={s.timeIcon}/>
            <div className={s.value}>{age}</div>
          </div>
          <div className={s.size}>
            <img src={diamondIcon} alt="" className={s.diamondIcon}/>
            <div className={s.value}>{size}</div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className={`${s.cards} ${className}`}>
      {
        blocksStore.blocks.map((_) => renderBlock(_))
      }
    </div>
  );
});
