import { Route, Switch } from "react-router-dom";
import { ExplorerPage } from "./components/pages/explorer/explorer-page";
import { ExplorerBlockPage } from "./components/pages/explorer-block/explorer-block-page";
import { routesPaths } from "./constants/routes";

export const useAppRoutes = () => {
  return (
    <Switch>
          <Route path={`${routesPaths.explorerBlock}:blockId`}>
            <ExplorerBlockPage/>
          </Route>
          <Route path={routesPaths.explorer}>
            <ExplorerPage/>
          </Route>
    </Switch>
  );
};
