import { useDimensions } from '../../../../hooks/use-dimensions';
import { getPageItemWidth } from './get-page-item-width';
import { RefObject } from 'react';
import { makeOdd } from '../../../../functions/make-odd';

const REQUIRED_COUNT = 7;
const REQUIRED_BUTTONS_WIDTH = 36 * 4;

export const useShowPageCount = (lastPage: number, paginatorRef: RefObject<HTMLDivElement>) => {
  const paginatorWidth = useDimensions(paginatorRef).width;
  const widthForPages = paginatorWidth - REQUIRED_BUTTONS_WIDTH;
  const pageWidth = getPageItemWidth(lastPage);

  const strippedCount = Math.floor(widthForPages / pageWidth);

  return Math.min(makeOdd(strippedCount), REQUIRED_COUNT);
};
