import s from './explorer.module.scss';
import { useForm } from 'react-hook-form';
import { Input } from '../../shared/inputs/input/input';
import { InfoItem } from './info-item/info-item';
import { Switch } from '../../shared/inputs/switch/switch';
import { BlocksTable } from './blocks-table/blocks-table';
import { useDebouncedValue } from '../../../hooks/use-debounced-value';
import { observer } from 'mobx-react-lite';
import { candyEventsStore } from '../../../store/candy/core/candy-events.store';
import { useEffect, useState } from 'react';
import { blocksStore } from '../../../store/candy/explorer/blocks.store';
import { Paginator } from '../../shared/paginator/paginator';
import { EventsTable } from './events-table/events-table';
import { getCountNodes, NODES } from 'api/get-count-nodes';
import { useQuery } from 'react-query';
import { candyStore } from 'store/candy/core/candy.store';
import {ReactComponent as Sollar} from '../../../assets/images/sollar.svg'

type ExplorerPageForm = {
  search: string
  hideEmptyBlocks: boolean
}

export const ExplorerPage = observer(() => {
  const { control, watch } = useForm<ExplorerPageForm>({
    defaultValues: {
      search: ''
    }
  });

  useEffect(() => {
    if(!candyStore.isReady){
      candyStore.start();
    }
  }, [])

  const { data: nodesCount } = useQuery(
    NODES,
    getCountNodes
  );
    
  const search = useDebouncedValue(watch('search'));
  const hideEmptyBlocks = watch('hideEmptyBlocks');

  const [selectedPage, setSelectedPage] = useState(1);

  useEffect(() => {
    const page = 1;
    setSelectedPage(page);
    blocksStore.fetchItems(search, hideEmptyBlocks, page);
  }, [search, hideEmptyBlocks]);

  useEffect(() => {
    blocksStore.fetchItems(search, hideEmptyBlocks, selectedPage);
  }, [selectedPage]);

  return (
    <article className={`${s.explorerPage} whiteCard`}>
      <div className={s.caption}>
       <Sollar className={s.icon}/><h1 className={s.heading1}>Sollar blockchain explorer</h1>
        <Input
          className={s.searchInput}
          control={control} name="search"
          placeholder="Search"
          isSearch
        />
        <div className={s.info}>
          <InfoItem name="Current connections:" value={nodesCount} isHighlighted/>
          <InfoItem name="Blockchain Height" value={candyEventsStore.blockHeight}/>
        </div>
        {
          !blocksStore.search &&
          <>
            <h2 className={s.heading2}>Latest Blocks</h2>
            <Switch className={s.toggle} control={control} name="hideEmptyBlocks" text="Hide empty blocks"/>
          </>
        }
      </div>
      {
        blocksStore.search
          ? <EventsTable className={s.blocksTable}/>
          : <BlocksTable className={s.blocksTable}/>
      }
      <Paginator
        className={s.paginator}
        selectedPage={selectedPage}
        setSelectedPage={setSelectedPage}
        totalCount={blocksStore.totalCount}
      />
    </article>
  );
});
