import { makeAutoObservable } from 'mobx';
import { ChainBlock } from '../../../types/history-item';
import { blocksStore } from '../explorer/blocks.store';

class CandyEventsStore {
  blockHeight = 0;
  connectionsCount = 0;

  constructor() {
    makeAutoObservable(this, {}, { autoBind: true });
  }

  onBlockHeightChange(height: number) {
    this.blockHeight = height;
  }

  onConnectionsCountChange(count: number) {
    this.connectionsCount = count;
  }

  onBlocksResponse(fetchedBlocks: ChainBlock[]) {
    blocksStore.onBlocksResponse(fetchedBlocks);
  }
}

export const candyEventsStore = new CandyEventsStore();
